import CloseIcon from '@mui/icons-material/Close';
import MenuIcon from '@mui/icons-material/Menu';
import { AppBar, Box, Button, Container, Drawer, IconButton, Toolbar, Typography, useScrollTrigger } from '@mui/material';
import { AnimatePresence, motion } from 'framer-motion';
import React, { useEffect, useState } from 'react';

const Header = () => {
  const [isScrolled, setIsScrolled] = useState(false);
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
  
  const trigger = useScrollTrigger({
    disableHysteresis: true,
    threshold: 100,
  });

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 50);
    };
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  const scrollToSection = (sectionId) => {
    const element = document.getElementById(sectionId);
    if (element) {
      const headerOffset = 80;
      const elementPosition = element.getBoundingClientRect().top;
      const offsetPosition = elementPosition + window.pageYOffset - headerOffset;

      window.scrollTo({
        top: offsetPosition,
        behavior: 'smooth'
      });
      setIsMobileMenuOpen(false);
    }
  };

  const navItems = [
    { label: 'About', id: 'problem-section' },
    { label: 'Process', id: 'process-section' },
    { label: 'Pricing', id: 'pricing-section' }
  ];

  return (
    <AnimatePresence>
      <motion.div
        initial={{ opacity: 0, y: -20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5 }}
      >
        <AppBar
          position="fixed"
          elevation={0}
          sx={{
            backgroundColor: isScrolled ? 'rgba(255, 255, 255, 0.9)' : 'rgba(138, 79, 255, 0.1)',
            backdropFilter: 'blur(10px)',
            boxShadow: isScrolled ? '0 4px 30px rgba(138, 79, 255, 0.1)' : 'none',
            transition: 'all 0.3s ease',
          }}
        >
          <Container maxWidth="lg">
            <Toolbar disableGutters>
              <motion.div
                whileHover={{ scale: 1.05 }}
                transition={{ type: "spring", stiffness: 400, damping: 10 }}
              >
                <Typography
                  variant="h6"
                  component="div"
                  sx={{
                    flexGrow: 1,
                    fontWeight: 800,
                    background: 'linear-gradient(135deg, #8A4FFF 0%, #A375FF 100%)',
                    WebkitBackgroundClip: 'text',
                    WebkitTextFillColor: 'transparent',
                    fontSize: { xs: '1.5rem', sm: '1.8rem' },
                    cursor: 'pointer',
                  }}
                  onClick={() => window.scrollTo({ top: 0, behavior: 'smooth' })}
                >
                  Kaicension
                </Typography>
              </motion.div>

              <Box sx={{ flexGrow: 1 }} />

              {/* Desktop Menu */}
              <Box sx={{ display: { xs: 'none', md: 'flex' }, gap: 2 }}>
                {navItems.map((item) => (
                  <motion.div
                    key={item.label}
                    whileHover={{ y: -2 }}
                    whileTap={{ scale: 0.95 }}
                  >
                    <Button
                      onClick={() => scrollToSection(item.id)}
                      sx={{
                        color: isScrolled ? '#1A1A1A' : '#8A4FFF',
                        fontWeight: 600,
                        position: 'relative',
                        backgroundColor: isScrolled ? 'transparent' : 'rgba(255, 255, 255, 0.9)',
                        padding: '8px 16px',
                        borderRadius: '20px',
                        '&:hover': {
                          backgroundColor: isScrolled ? 'rgba(138, 79, 255, 0.1)' : 'rgba(255, 255, 255, 1)',
                        },
                      }}
                    >
                      {item.label}
                    </Button>
                  </motion.div>
                ))}
                <motion.div
                  whileHover={{ scale: 1.05 }}
                  whileTap={{ scale: 0.95 }}
                >
                  <Button
                    variant="contained"
                    onClick={() => scrollToSection('pricing-section')}
                    sx={{
                      background: 'linear-gradient(135deg, #8A4FFF 0%, #A375FF 100%)',
                      color: 'white',
                      fontWeight: 600,
                      '&:hover': {
                        background: 'linear-gradient(135deg, #A375FF 0%, #8A4FFF 100%)',
                      },
                    }}
                  >
                    Get Started
                  </Button>
                </motion.div>
              </Box>

              {/* Mobile Menu Button */}
              <IconButton
                sx={{ 
                  display: { xs: 'flex', md: 'none' },
                  color: isScrolled ? '#1A1A1A' : '#8A4FFF'
                }}
                onClick={() => setIsMobileMenuOpen(true)}
              >
                <MenuIcon />
              </IconButton>
            </Toolbar>
          </Container>
        </AppBar>

        {/* Mobile Menu Drawer */}
        <Drawer
          anchor="right"
          open={isMobileMenuOpen}
          onClose={() => setIsMobileMenuOpen(false)}
          PaperProps={{
            sx: {
              width: '100%',
              maxWidth: '300px',
              background: 'linear-gradient(135deg, #ffffff 0%, #f5f7fa 100%)',
              padding: '2rem',
            }
          }}
        >
          <Box sx={{ display: 'flex', justifyContent: 'flex-end', mb: 2 }}>
            <IconButton onClick={() => setIsMobileMenuOpen(false)}>
              <CloseIcon />
            </IconButton>
          </Box>
          <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
            {navItems.map((item) => (
              <Button
                key={item.label}
                onClick={() => scrollToSection(item.id)}
                sx={{
                  color: '#1A1A1A',
                  fontWeight: 600,
                  fontSize: '1.1rem',
                  padding: '12px',
                  borderRadius: '10px',
                  '&:hover': {
                    backgroundColor: 'rgba(138, 79, 255, 0.1)',
                  },
                }}
              >
                {item.label}
              </Button>
            ))}
            <Button
              variant="contained"
              onClick={() => scrollToSection('pricing-section')}
              sx={{
                background: 'linear-gradient(135deg, #8A4FFF 0%, #A375FF 100%)',
                color: 'white',
                fontWeight: 600,
                padding: '12px',
                fontSize: '1.1rem',
                mt: 2,
                '&:hover': {
                  background: 'linear-gradient(135deg, #A375FF 0%, #8A4FFF 100%)',
                },
              }}
            >
              Get Started
            </Button>
          </Box>
        </Drawer>
      </motion.div>
    </AnimatePresence>
  );
};

export default Header; 